body {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans Condensed', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(78, 78, 83);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.logo {
  background-color: #ac8836; }
  .logo .photo {
    transform: scale(0, 0); }
  .logo .placeholder {
    transition: all 0.3s ease-in-out; }
  .logo .contact svg {
    transform: scale(0, 0); }

.logo.focused {
  transform-origin: top left;
  transform: scale(1.3, 1.3);
  position: relative; }
  .logo.focused .placeholder {
    position: relative;
    width: 30px !important;
    height: 30px !important; }
  .logo.focused .container {
    padding-right: unset;
    display: flex;
    flex-direction: column;
    text-align: center;
    align-items: center; }
    .logo.focused .container .header {
      margin-bottom: unset !important;
      background-color: unset !important; }
    .logo.focused .container .photo {
      transform: scale(1, 1);
      margin: 10px; }
      .logo.focused .container .photo img {
        border-radius: 50%;
        height: 50px;
        width: 50px; }
  .logo.focused .contact {
    position: absolute;
    right: 0;
    bottom: 0;
    margin-right: 5px;
    margin-bottom: 2px; }
    .logo.focused .contact svg {
      color: #474040;
      padding: 2px;
      width: 25px;
      height: 25px;
      transform: scale(1, 1);
      transition: all 0.1s ease-in-out; }
      .logo.focused .contact svg:hover {
        transform: scale(1.1, 1.1);
        text-shadow: 0px 0px 10px 10px black; }

.education {
  background-color: #927a5c; }
  .education .container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 10px 5px;
    text-align: center;
    transform: scale(0, 0); }
    .education .container .info {
      padding-top: 10%;
      text-align: left; }
    .education .container .college {
      padding-bottom: 10px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start; }

.education.focused {
  transform: scale(1.7, 1.5);
  transform-origin: top; }
  .education.focused .placeholder {
    width: 30px !important;
    height: 30px !important;
    transform: rotate(-20deg); }
  .education.focused .name {
    background-color: rgba(190, 190, 18, 0.164);
    padding: 2px 4px 1px 2px;
    display: inline-flex;
    width: auto;
    border-radius: 1px; }
  .education.focused .desc {
    font-size: 8px; }

.work-experience {
  background-color: rgba(160, 65, 48, 0.945); }
  .work-experience .placeholder {
    width: 90% !important;
    height: 80% !important;
    color: rgba(129, 123, 123, 0.445) !important; }
  .work-experience .container {
    width: 100%;
    height: 100%;
    padding: 10px 5px;
    transition: all 0.3s ease-in-out;
    transform: scale(0, 0); }
    .work-experience .container .company {
      display: flex;
      flex-direction: column;
      justify-content: flex-start; }

.work-experience.focused {
  transform: scale(1.7, 1.5);
  transform-origin: top right;
  opacity: 1; }

.work-experience.focused {
  display: inline-flex !important; }
  .work-experience.focused .placeholder {
    padding: 2px 4px;
    position: relative;
    width: 20px !important;
    height: 30px !important;
    top: 1%;
    left: 1%; }
  .work-experience.focused .name {
    background-color: rgba(190, 190, 18, 0.164);
    padding: 2px 4px 1px 2px;
    display: inline-flex;
    width: auto;
    border-radius: 1px; }
  .work-experience.focused .location {
    font-size: 7px; }

.projects {
  background-color: #3f6363;
  color: #d3cfcf; }
  .projects .placeholder {
    width: 80% !important;
    height: 80% !important;
    color: rgba(129, 123, 123, 0.445) !important; }
  .projects .container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 10px 5px;
    text-align: center;
    transform: scale(0, 0); }
    .projects .container .project {
      padding-bottom: 10px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start; }

.projects.focused {
  transform: scale(1.5, 1.5);
  transform-origin: bottom left; }
  .projects.focused .placeholder {
    width: 35px !important;
    height: 35px !important; }
  .projects.focused .name {
    background-color: rgba(190, 190, 18, 0.164);
    padding: 2px 4px 1px 2px;
    display: inline-flex;
    width: auto;
    border-radius: 1px; }

.photography {
  background-color: #333;
  color: #d3cfcf; }
  .photography .placeholder {
    height: 110% !important; }

.photography.focused {
  transform: scale(2, 1.8);
  transform-origin: bottom right; }
  .photography.focused .placeholder {
    width: 25px !important;
    height: 25px !important;
    transform: rotate(360deg); }

.technology-stack {
  background-color: #3e725e;
  opacity: 1;
  color: #d3cfcf; }
  .technology-stack .placeholder {
    width: 80% !important;
    height: 80% !important;
    color: rgba(129, 123, 123, 0.445) !important; }
  .technology-stack .container {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 10px 5px;
    text-align: center;
    transform: scale(0, 0); }
    .technology-stack .container .company {
      padding-bottom: 10px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start; }

.technology-stack.focused {
  transform: scale(1.5, 1.5);
  transform-origin: bottom; }
  .technology-stack.focused .placeholder {
    width: 30px !important;
    height: 30px !important; }
  .technology-stack.focused .name {
    background-color: rgba(190, 190, 18, 0.164);
    padding: 2px 4px 1px 2px;
    display: inline-flex;
    width: auto;
    border-radius: 1px; }

.app {
  box-sizing: border-box;
  font-size: 10px;
  display: flex;
  height: 100vh;
  flex-wrap: wrap;
  justify-content: space-evenly; }
  .app section {
    margin: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
    width: 32%;
    height: 50%;
    position: relative;
    transition: all 0.3s ease-in-out;
    transform: scale(1, 1);
    opacity: 1; }
    .app section .placeholder {
      position: absolute !important;
      width: 90% !important;
      height: 90% !important;
      margin: auto;
      color: rgba(105, 98, 98, 0.514);
      transition: all 0.3s ease-in-out; }
      .app section .placeholder path {
        text-shadow: 0px 0px 20px 4px black; }
    .app section .container {
      overflow-y: auto !important;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      padding: 0% 15% !important;
      transition: all 0.3s ease-in-out;
      text-align: left;
      transform: scale(0, 0); }
      .app section .container .header {
        margin-top: 2%;
        margin-bottom: 5%;
        font-size: 14px; }
      .app section .container .info {
        padding-top: 5px; }
  .app section.focused {
    z-index: 5;
    opacity: 1;
    border-radius: 8px;
    box-shadow: 0px 0px 30px 1px #535050; }
    .app section.focused .placeholder {
      position: relative;
      top: 1%;
      left: 1%; }
      .app section.focused .placeholder path {
        width: 10%;
        height: 10%; }
    .app section.focused .container {
      transform: scale(1, 1); }
      .app section.focused .container .header {
        background-color: rgba(138, 190, 18, 0.164);
        padding: 2px 4px 1px 2px;
        display: inline-flex;
        width: auto;
        border-radius: 1px; }
  .app section.shaded {
    opacity: 0.2; }
    .app section.shaded .photo {
      display: none; }

ul {
  padding-left: 10px; }
  ul li {
    font-size: 8px; }

